<template>
	<div class="dengluzhuce">
		<!-- <div class="pm"></div> -->
		<div class="zuoj"><img src="../../assets/denglu/zuoj.png" alt="" onclick="window.history.go(-1)"></div>
		<div class="lg" align="center">
			<img src="../../assets/denglu/LOGO.png">
		</div>
		<div class="denglu" align="center">
			<input v-model="username" type="text" placeholder="请输入昵称">
			
			<input v-model="password" type="text" placeholder="密码">
		</div>
		<div class="youke" align="center">
			<button @click="login">登录</button>
		</div>
		<div class="qita" align="center">其它登录方式</div>
		<div class="qq" align="center">
			
			<ul>
				<li><a href=""><img src="../../assets/denglu/qq.png"><p>QQ</p></a></li>
				<li><a href=""><img src="../../assets/denglu/weixin.png"><p>微信</p></a></li>
				<li><a href=""><img src="../../assets/denglu/weibo.png"><p>微博</p></a></li>
			</ul>
		</div>

		
	</div>
</template>

<script>

export default{
	name:"",
	data(){
		return{
			username:"",
			password:""
		}
     },
    methods:{
    	login(){
    		var url=this.aa+"/selectinfo"
    		console.log(this.username)
    		this.$axios.post(url,{
    			username:this.username,
    			password:this.password
    		})
    		.then(res=>{
    			console.log(res)
    			var tem=res.data.code
    			if(tem==100){
    				alert("登录成功")
    				window.location.href="http://localhost:8080/#/index/yinmu/"
    			}else if(tem==200){
    				alert("用户名或密码错误")
    			}else if(tem==300){
    				alert("用户不存在,请注册")
    				window.location.href="http://localhost:8080/jinzhuce"
    			}
    		})
    	}
    }
}
</script>

<style scoped lang="less">
.template{

}
.dengluzhuce{
	background: #fff;
}
.pm{
	width: 750/100rem;
	height: 48/100rem;
	background: blue;
}
.zuoj{
	width: 24/100rem;
	height: 38/100rem;
	margin-left: 12/100rem;
}
.lg{
	width: 750/100rem;
	height: 218*2/100rem;
	
	display:table-cell;
	vertical-align:middle;
	text-align:center;
	&>img{
		width: 202/100rem;
		height: 185/100rem;
		vertical-align:middle;	
	}
}
.denglu{
	width: 5.5rem;
    height: 2.08rem;
    margin: 0 auto;
    
	 input{
		width: 236*2/100rem;
		height: 38*2/100rem;
		line-height:38*2/100rem; 
		margin-top: 15*2/100rem;
		padding-left: 0.8rem;
		background: #fff;
		color: #C0C0C0;
		border: none;
		border-bottom: 1px solid #C0C0C0;
		font-size: 16*2/100rem;
		background: url(../../assets/denglu/shouji.png )no-repeat left ;
		background-size: 0.4rem 0.5rem;
		outline: none;		 
	}
	  input:last-child{ 
                background-image: url(../../assets/denglu/suo.png );
            }
	
}
.youke{
	width: 750/100rem;
	height: 130*2/100rem;
	margin-top: 28*2/100rem;
	button{
	width: 228*2/100rem;
	height: 40*2/100rem;
	color:#A381F4; 
	background: #fff;
	border: 1px solid #A381F4;
	border-radius: 1rem; 
	outline: none;
	font-size:32/100rem;
	}
	
}
.qita{
	width: 750/100rem;
	height: 40*2/100rem;
	
	font-size: 13*2/100rem;
}
.qq{
	width: 636/100rem;
	height: 58*2/100rem;
	font-size: 13*2/100rem;
	&>ul li img{
	width: 80/100rem;
	height: 80/100rem;
	display: inline-block;

	}
	
}
.qq li{
	display: inline-block;
	margin-left: 57*2/100rem;
}	
</style>


